            header {
                grid-area: header;
                position: sticky;
                grid-column-start: 2;
                grid-column-end:5 ;
                top: 0; 
                left: 0;
                width: 100%;
                background-color: #132b26;
                color: white;
                text-align: center;
               
                transition: top 0.3s ease; 
                z-index: 1000; 
                display: grid;
                place-items: center;
                height: 3rem;
                border: 1px solid black;
                display: flex;
                align-items: center;
                justify-content: space-between;
                

              
               
              }

              header h2{
                color: aliceblue;
                font-size: 2.1rem;
                padding: 1rem;
              }

              header a{
                color: aliceblue;
                font-size: 1.5rem;
                padding-right: 3rem;
              }
              footer  {
                display: flex;
                grid-area: footer;
                position: fixed;
                bottom: 0; 
                left: 0;
                width: 100%;
                background-color: #132b26;
                color: white;
                text-align: start;
               
                transition: top 0.3s ease; 
                z-index: 1000; 
               
                place-items: center;
                height: 3rem;
                align-items: center;
                justify-content: center;
                font-size: 1rem;
                padding-left: 2rem;
                gap:2rem;


                
               
